<template>
    <transition mode="out-in" name="slide-fade" appear>
        <footer :class="{ 
            'dividend-page': this.$store.getters.dataLoaded,
            '' : !this.$store.getters.dataLoaded,
        }">
            <div class="px-3 px-lg-4 py-2">
                <div class="row">
                    <div class="d-none d-lg-block col-12 col-lg-4 col-xl-2 order-1 order-lg-3">
                        <a style="text-decoration: none;" href="https://paypal.me/rajaruban" target="_blank">
                            <button class="btn btn-secondary donate-btn text-dark w-100" style="background: #FFC439; border: 1px solid #FFC439;">
                                <strong>
                                    Support us via
                                    <em>
                                        <span style="color:#003087;">Pay</span>
                                        <span style="color:#009CDE;">Pal</span>
                                    </em>
                                </strong>
                            </button>
                        </a>
                    </div>
                    <div class="col-lg-5 col-xl-7 order-2"></div>
                    <div class="col-12 col-lg-3 text-center text-lg-start order-3 order-lg-1">
                        <p class="my-2">2022 &copy; SavaJava<sup><sup>TM</sup></sup></p>
                    </div>
                </div>
            </div>
        </footer>
    </transition>
</template>
<script>
export default {
    data(){
        return {
            isMobile: null,
        };
    },
    mounted(){
        // logic to activate this function whenever screen is resized
        window.addEventListener("resize", this.checkIsMobile);
    },
    created(){
        // logic to activate this function whenever screen is resized
        window.addEventListener("resize", this.checkIsMobile);
    },
    unmounted(){
        // logic to deactivate this function whenever screen is resized
        window.removeEventListener("resize", this.checkIsMobile);
    },
    computed: {
        dataLoaded(){
            return this.$store.getters.dataLoaded;
        },
    },
    methods:{
        checkIsMobile(){
            console.log(window.innerWidth);
            if(window.innerWidth < 992){
                this.isMobile = true;
            } else {
                this.isMobile = false;
            }
        },
    },
};
</script>
<style scoped>
footer {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 0;
}

.smart-button-container {
    height: 100vh;
}

footer.dividend-page {
    position: static;
}

.light .form-validation-alert {
    color: #333;
}
</style>
